* {
  margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

body {
	background-color: antiquewhite;
}

.main-scene {
	width: 100%;
    height: 100%;
    border-radius: 0;
	background-color: #fff;
}

.animation-container {
  position: absolute;
  width: 100%;
  height: 100%;
}

.start-button {
	border: 2px solid #131c98;
	color: #131c98;
	border-radius: 5px;
	font-size: 24px;
	background-color: #fff;
	position: absolute;
	top: 50%;
	left: 50%;
	padding: 10px 20px;
	transform: translate(-50%, -50%);
}

.start-button:hover {
	color: #27a4d6;
	border: 2px solid #27a4d6;
	cursor: pointer;
}

@keyframes change-size {
	from {
			height: 0%;
			width: 0%;
	}
	to {
			height: 100%;
			width: 100%;
	}
}